.wrapper {

  span {
    color: white;
    display: block;
    margin-top: 60px;
  }

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .container {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .ring {
      position: relative;
      width: 150px;
      height: 150px;
      margin: -30px;
      border: 4px solid transparent;
      border-radius: 50%;
      border-top: 4px solid #24ecff;

      &::before {
        content: '';
        position: absolute;
        top: 12px;
        right: 12px;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: #24ecff;
        box-shadow: 0 0 0 5px #24ecff33, 0 0 0 10px #24ecff22, 0 0 0 20px #24ecff11, 0 0 40px #24ecff, 0 0 50px #24ecff;
      }

      &:nth-child(1) {
        animation: circle2 2s linear infinite;
      }

      &:nth-child(2) {
        animation: circle1 2s linear infinite;
      }

      &:nth-child(3) {
        position: absolute;
        top: -66.66px;
        animation: circle 2s linear infinite;
      }
    }

  }
}


@keyframes circle {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes circle1 {
  0% {
    transform: rotate(120deg);
  }
  100% {
    transform: rotate(480deg);
  }
}

@keyframes circle2 {
  0% {
    transform: rotate(240deg);
  }
  100% {
    transform: rotate(600deg);
  }
}
